.btn-transparent {
  cursor: pointer;
  letter-spacing: .02em;
  color: #f3c653;
  background: #0006;
  border: 2px solid #fff;
  border-radius: 25px;
  padding: 12px 22px;
  font-weight: 700;
  line-height: 1.25;
  transition: transform .25s cubic-bezier(.65, .05, .36, 1), background-color .25s cubic-bezier(.65, .05, .36, 1), border-color .25s cubic-bezier(.65, .05, .36, 1);
  transform: scale(1);
}

.btn-transparent:hover, .btn-transparent:focus {
  background: #fff;
  border: 2px solid #f3c653;
  transform: scale(1.1);
}

.scroll-up {
  opacity: 1;
  visibility: visible;
  z-index: 99;
  width: 40px;
  height: 64px;
  transition: opacity .25s cubic-bezier(.65, .05, .36, 1), visibility .25s cubic-bezier(.65, .05, .36, 1);
  position: fixed;
  bottom: 30px;
  right: 30px;
}

