  .scroll-up {
    display: none;
  }
}

.scroll-up__link {
  width: 100%;
  height: 100%;
  color: #f3c653b3;
  justify-content: center;
  align-items: center;
  transition: color .25s cubic-bezier(.65, .05, .36, 1), transform .25s cubic-bezier(.65, .05, .36, 1);
  display: flex;
}

.scroll-up__link:hover, .scroll-up__link:focus {
  color: #f3c653;
  transform: scale(1.1);
}

.scroll-up__img {
  fill: currentColor;
}

.scroll-up.scroll-up__hide {
  opacity: 0;
  visibility: hidden;
}

